@import 'Styles/color.scss';
.footer {
  flex-shrink: 0;
  background-color: $mine-shaft;
  color: $white;
  display: flex;
  flex-direction: row;
  justify-content: center;
  &__disc {
    height: 30px;
    width: 30px;
    margin: 0 10px;
    background: transparent;
    box-shadow: none;
    line-height: 30px;
    font-size: medium;
  }
}
