@-webkit-keyframes anim-bounce-in {
  0% {
    opacity: 0;
    -webkit-transform: scale(0.5); }
  100% {
    -webkit-transform: scale(1); } }

@keyframes anim-bounce-in {
  0% {
    opacity: 0;
    -webkit-transform: scale(0.5);
    transform: scale(0.5); }
  100% {
    -webkit-transform: scale(1);
    transform: scale(1); } }

@-webkit-keyframes anim-lightspeed-in {
  0% {
    -webkit-transform: translateX(-200%);
            transform: translateX(-200%);
    opacity: 1; }
  100% {
    -webkit-transform: translateX(0);
            transform: translateX(0);
    opacity: 1; } }

@keyframes anim-lightspeed-in {
  0% {
    -webkit-transform: translateX(-200%);
            transform: translateX(-200%);
    opacity: 1; }
  100% {
    -webkit-transform: translateX(0);
            transform: translateX(0);
    opacity: 1; } }

@-webkit-keyframes anim-spinner {
  50% {
    -webkit-filter: blur(2px);
            filter: blur(2px); }
  90%, 100% {
    -webkit-transform: rotate(1turn);
            transform: rotate(1turn);
    -webkit-filter: blur(0);
            filter: blur(0); } }

@keyframes anim-spinner {
  50% {
    -webkit-filter: blur(2px);
            filter: blur(2px); }
  90%, 100% {
    -webkit-transform: rotate(1turn);
            transform: rotate(1turn);
    -webkit-filter: blur(0);
            filter: blur(0); } }

/*change this to resize*/
.Spinner {
  top: 0;
  left: 0;
  z-index: 999;
  position: absolute;
  width: 100vw;
  height: 100vh;
  background: rgba(255, 255, 255, 0.5);
  display: flex;
  justify-content: center;
  align-items: center; }
  .Spinner span {
    position: absolute;
    margin: auto;
    border-radius: 50%;
    box-sizing: border-box;
    display: flex;
    justify-content: center;
    align-items: center;
    -webkit-transform-origin: center center;
            transform-origin: center center;
    -webkit-animation: anim-spinner 1s ease-in-out infinite;
            animation: anim-spinner 1s ease-in-out infinite;
    will-change: transform, filter; }
    .Spinner span:nth-child(1) {
      width: 1em;
      height: 1em; }
      .Spinner span:nth-child(1):after {
        content: '';
        display: block;
        font-size: 25%;
        width: 1em;
        height: 1em;
        border-radius: 50%;
        box-shadow: 1em 0 0 rgba(72, 157, 138, 0.2), -1em 0 0 rgba(50, 70, 80, 0.2), 0 1em 0 rgba(211, 75, 76, 0.2), 0 -1em 0 rgba(232, 169, 97, 0.2); }
    .Spinner span:nth-child(2) {
      -webkit-animation-delay: .1s;
              animation-delay: .1s;
      width: 2em;
      height: 2em; }
      .Spinner span:nth-child(2):after {
        content: '';
        display: block;
        font-size: 50%;
        width: 1em;
        height: 1em;
        border-radius: 50%;
        box-shadow: 1.5em 0 0 rgba(72, 157, 138, 0.4), -1.5em 0 0 rgba(50, 70, 80, 0.4), 0 1.5em 0 rgba(211, 75, 76, 0.4), 0 -1.5em 0 rgba(232, 169, 97, 0.4); }
    .Spinner span:nth-child(3) {
      -webkit-animation-delay: .15s;
              animation-delay: .15s;
      width: 3em;
      height: 3em; }
      .Spinner span:nth-child(3):after {
        content: '';
        display: block;
        font-size: 75%;
        width: 1em;
        height: 1em;
        border-radius: 50%;
        box-shadow: 2em 0 0 rgba(72, 157, 138, 0.6), -2em 0 0 rgba(50, 70, 80, 0.6), 0 2em 0 rgba(211, 75, 76, 0.6), 0 -2em 0 rgba(232, 169, 97, 0.6); }
    .Spinner span:nth-child(4) {
      -webkit-animation-delay: .2s;
              animation-delay: .2s;
      width: 4em;
      height: 4em; }
      .Spinner span:nth-child(4):after {
        content: '';
        display: block;
        font-size: 100%;
        width: 1em;
        height: 1em;
        border-radius: 50%;
        box-shadow: 2.5em 0 0 rgba(72, 157, 138, 0.8), -2.5em 0 0 rgba(50, 70, 80, 0.8), 0 2.5em 0 rgba(211, 75, 76, 0.8), 0 -2.5em 0 rgba(232, 169, 97, 0.8); }
    .Spinner span:nth-child(5) {
      -webkit-animation-delay: .25s;
              animation-delay: .25s;
      width: 5em;
      height: 5em; }
      .Spinner span:nth-child(5):after {
        content: '';
        display: block;
        font-size: 125%;
        width: 1em;
        height: 1em;
        border-radius: 50%;
        box-shadow: 3em 0 0 #489D8A, -3em 0 0 #324650, 0 3em 0 #D34B4C, 0 -3em 0 #E8A961; }

.main__content {
  flex-grow: 1;
  overflow: auto;
